import { useContext, useEffect, useState } from "react";
import styled, { css } from "styled-components";
import Styles, { _CSS_LABEL_MIDIUM } from "../../base/design/styles";
import DataUtil from "../../utils/dataUtil";
import { HOME_USER_DIV_HEIGHT } from "../../utils/systemConst";
import { GlobalContext } from "../entry/systemEntry";

type SeedProps = {
    name: string;
    ownerId: string;
    lastmod: string;
}

const PublishFrame = (props: {
    setArtsCnt: (artsCnt: number) => void;
}) => {

    const { store, setStore } = useContext(GlobalContext);

    const [seedList, setSeedList] = useState<SeedProps[]>([]);

    useEffect(() => {
        findAllReleaseProjectList().then((resList) => {
            const seedList: SeedProps[] = [];
            resList.forEach(res => {
                const name = res.name;
                const ownerId = res.owner;
                const lastmod = res.lastmod;
                seedList.push({
                    name, ownerId, lastmod
                });
            });
            setSeedList(seedList);
            props.setArtsCnt(seedList.length);
        });
    }, []);

    const seedListJsx = seedList.map((seed, i) => {
        return (<>
            <_CloudItem key={i} onClick={() => {
                // window.open(DataUtil.getPreviewURL(score.seq), '_blank');
            }}>
                <_Owner>{`${seed.ownerId}`}</_Owner>
                <_CName>{`${seed.name}`}</_CName><_CDate>{seed.lastmod}</_CDate>
            </_CloudItem>
        </>);
    });

    return (
        <_ListFrame>{seedListJsx}</_ListFrame>
    );
}

export default PublishFrame;

export const findAllReleaseProjectList = async () => {
    const response = await DataUtil.sendQueryRequestToAPI('select', `
        SELECT name, seq, (select id from user_tbl where seq = prj.owner) as owner, source, lastmod
        FROM prjcttbl prj
        WHERE source <> ''
        ORDER BY lastmod desc
    `);
    const results = await response.json();
    return results as any[];
};

